
document.addEventListener('DOMContentLoaded', function (){

  program.init();
});


var program = (function(){
  let container;
  let videoData;

  /*Tekur inn millisekndur og breytir eim 
    strstu mgulega einingu(t.d. mntur, klukkustundur)
    og skilar streng sem lsir v*/
  function millisecondsToString(milliseconds){
    const sec = 1000;
    const min = 60*sec;
    const hour = 60*min;
    const day = 24*hour;
    const week = 7*day;
    const month = 30*day;
    const year = 365*day;
    let time;
    if(milliseconds < min){
      time = Math.floor(milliseconds/sec);
      if(time != 1)
        return "Fyrir " + time + " sekndum san";
      else{
        return "Fyrir " + time + " sekndu san";
      }
    }
    else if(milliseconds < hour){
      time = Math.floor(milliseconds/min);
      if(time != 1)
        return "Fyrir " + time + " mntum san";
      else{
        return "Fyrir " + time + " mntu san";
      }
    }

    else if(milliseconds < day){
      time = Math.floor(milliseconds/hour);
      if(time != 1)
        return "Fyrir " + time + " klukkustundum san";
      else{
        return "Fyrir " + time + " klukkustund san";
      }
    }

    else if(milliseconds < week){
      time = Math.floor(milliseconds/day);
      if(time != 1)
        return "Fyrir " + time + " dgum san";
      else{
        return "Fyrir " + time + " degi san";
      }
    }

    else if(milliseconds < month){
      time = Math.floor(milliseconds/week);
      if(time != 1)
        return "Fyrir " + time + " vikum san";
      else{
        return "Fyrir " + time + " viku san";
      }
    }

    else if(milliseconds < year){
      time = Math.floor(milliseconds/month);
      if(time != 1)
        return "Fyrir " + time + " mnuum san";
      else{
        return "Fyrir " + time + " mnui san";
      }
    }

    else{
      time = Math.floor(milliseconds/year);
      if(time != 1)
        return "Fyrir " + time + " rum san";
      else{
        return "Fyrir " + time + " ri san";
      }
    }
  }

  /*Tekur inn millisekndurnur sem hfu lii fr
    1. jan 1970 anga til myndbandi var sett inn
    og skilar streng sem segir hva er langt san
    myndbandi var sett inn*/
  function timeSinceCreated(created){
    const oldDate = new Date(created);
    const newDate = Date.now();
    const difference = newDate - oldDate;
    return millisecondsToString(difference);
  }

  function secondToMinutesAndSeconds(totalSeconds){
    const min = 60;
    let minutes, seconds;
    minutes = Math.floor(totalSeconds/min);
    seconds = totalSeconds % min;
    if(seconds < 10)
      seconds = "0" + seconds;
    return minutes + ":" + seconds;
  }


  function element(name, child) {
    const el = document.createElement(name);

    if (typeof child === 'string') {
      el.appendChild(document.createTextNode(child));
    } else if (typeof child === 'object') {
      el.appendChild(child)
    }
    return el;
  }

  function loadJSON() {
    const r = new XMLHttpRequest();
    r.open('GET', 'videos.json', true);
    r.onload = function() {
      var data = JSON.parse(r.response);
      if (r.status >=200 && r.status < 400) {
          addVideoList(data);
      } else {
        //villuskilabo
      }
    };

    r.send();
   }

   function addPoster(video){
     let videoPoster = element('img');
     videoPoster.src = video.poster;
     videoPoster.className = 'vidimg';
     return videoPoster;
   }
   function addTitle(video){
     let videoTitle = element('p', video.title);
     videoTitle.className = 'vidtitle';
     return videoTitle;
   }
   function addCreated(video){
     let videoCreated = element('p', timeSinceCreated(video.created));
     videoCreated.className = 'vidcreated';
     return videoCreated;
   }


  /*tekur inn json object sem hefur veri parse-a, br til div elements fyrir
    hvert category, setur inn titil fyrir hvert category og birtir allar upplsingar
    fyrir hvert myndband.*/
  function addVideoList(data){
    let videoData = data; //json objecti

    /*Br til div fyrir hvert category og inni  v divi
      er bi til div utan um hverja mynd og texta tilheyrandi
      eirri mynd*/
    for(let i = 0; i < videoData.categories.length; i++){
      let divTitle = element('h2', videoData.categories[i].title);
      divTitle.className = 'categorytitle';
      let div = element('div', divTitle);
      div.className = 'category';
      for(let j = 0; j < videoData.categories[i].videos.length; j++){
        let innerDiv = element('div');
        innerDiv.className = 'imagediv';
        let videoID = videoData.categories[i].videos[j];
        let video = videoData.videos[videoID-1];
        let videoPoster = addPoster(video);
        let videoTitle = addTitle(video);
        let videoCreated = addCreated(video);
        let linkToVideo = element('a');
        linkToVideo.href = "video.html?id=" + video.id;
        let imageContainer = element('div');
        imageContainer.className = 'imagecontainer';
        let thumbnail = element('div', '' +
        secondToMinutesAndSeconds(video.duration));
        thumbnail.className = 'thumbnail';
        linkToVideo.appendChild(videoPoster);
        imageContainer.appendChild(linkToVideo);
        imageContainer.appendChild(thumbnail);
        innerDiv.appendChild(imageContainer);
        innerDiv.appendChild(videoTitle);
        innerDiv.appendChild(videoCreated);
        div.appendChild(innerDiv);
      }
      container.appendChild(div);

      //Setur border  milli categories
      let borderContainerDiv = element('div');
      borderContainerDiv.className = 'bordercontainerdiv';
      let borderDiv = element('div');
      borderDiv.className = 'borderdiv';
      borderContainerDiv.appendChild(borderDiv);
      container.appendChild(borderContainerDiv);
    }

  }

  function init(){
    container = document.querySelector('.container');
    loadJSON();
  }


return {
  init: init
}

})();
